<template lang="html">
  <div class="row nestedDemo horizontal">
    <div class="alert alert-success">
      <b>배치도 변경시</b> : 새로운 장비의 버튼을 드레그 & 드롭해서 필요한 위치에 마우스를 놓으면 해당 장비 및 방이 생깁니다. <br>
      <b>예약 정보확인 시</b> : 해당 룸을 클릭하면 상세 예약자 명이 오른쪽 예약고객에 표시됩니다.<br>
      <b>예약가능 영역</b> : 예약가능 영역에 있는 룸/장치들은 온라인에서 예약이 가능합니다.<br>
      <b>로드고객용 영역</b> : 로드고객용 영역은 온라인에서 예약이 불가능한 장치 및 룸을 표시합니다.
      <br>
    </div>
    <div v-for="(zone, list) in dropzones" class="col-md-5">
      <div class="panel panel-vue">
        <div class="panel-heading">
            <h3 class="panel-title">{{zone}}</h3>
        </div>
        <ul class="panel-body container-list"
            v-dnd-list
            :dnd-list="list"
            dnd-inserted="inserted"
            dnd-effect-allowed="move"
            :dnd-disable-if="disable"
            :dnd-external-sources="true">
          <list v-for="item in list" :item="item" :list="list" :index="$index" :selected.sync="selected" :disable.sync="disable"></list>
        </ul>
      </div>
    </div>
    <div class="col-md-2">
      <div class="new-elements">
        <div class="panel panel-info">
          <div class="panel-heading">
              <h3 class="panel-title">장비추가</h3>
          </div>
          <div class="panel-body">
            <div v-dnd-draggable class="dnd-button"
                :dnd-draggable="templates"
                dnd-copied="copied"
                dnd-effect-allowed="copy">
                장비 추가
            </div>
          </div>
          <div class="panel-body">
            <div v-dnd-draggable class="dnd-button"
                :dnd-draggable="templates2"
                dnd-copied="copied"
                dnd-effect-allowed="copy">
                룸 추가
            </div>
          </div>
        </div>
      </div>
      <div class="selected" v-if="selected">
        <div class="panel panel-info">
          <div class="panel-heading">
              <h3 class="panel-title">선택된 장비</h3>
          </div>
          <div class="panel-body">
            {{selected.type}} {{selected.id}}
          </div>
          <div class="panel panel-info">
            <div class="panel-heading">
                <h3 class="panel-title">예약고객</h3>
            </div>
            <div class="panel-body">
              홍길동(7775)
            </div>
          </div>
        </div>
      </div>
      <div class="ashcan">
        <div class="panel panel-info">
          <div class="panel-heading">
              <h3 class="panel-title">장비제거</h3>
          </div>
          <div v-dnd-list  :dnd-list="[]" class="panel-body">
            <img class="ashcan-logo" src="../assets/ashcan.png" alt="" />
          </div>
        </div>
      </div>
      <div class="new-elements disable-element">
        <div class="panel panel-info">
          <div class="panel-heading">
              <h3 class="panel-title">배치도변경</h3>
          </div>
          <div class="panel-body">
            <div class="dnd-button" @click="toggleDisable">
              <span v-if="disable">고정</span>
              <span v-else>변경가능</span>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>

import list from '../components/list';

export default {
  data() {
    let lists = [];
    let id = 10;
    for(var i = 0; i < 3; i++) {
      lists.push([]);
      for(var j = 0; j < 2; j++) {
        lists[i].push([]);
        for(var k = 0; k < 7; k++) {
          lists[i][j].push({"label": "Item " + id++});
        }
      }
    }
    return {
      "lists": lists,
      selected: null,
      templates2: {
          "type": "container",
          "id": 1,
          "columns": []},
      templates: {type: "새로운 장비", id: 1},
      disable: false,
      dropzones: {
        "예약가능": [
          {
            "type": "container",
            "id": 1,
            "columns": [
                {
                  "type": "장비",
                  "id": "1"
                },
                {
                  "type": "장비",
                  "id": "2"
                },
                {
                  "type": "장비",
                  "id": "3"
                }
            ]
          },
          {
            "type": "장비",
            "id": "4"
          },
          {
            "type": "장비",
            "id": "5"
          },
          {
            "type": "장비",
            "id": "6"
          }
        ],
        "로드고객용(예약불가능)": [
          {
            "type": "장비",
            "id": 7
          },
          {
            "type": "장비",
            "id": "8"
          },
          {
            "type": "container",
            "id": "2",
            "columns": [
                {
                  "type": "장비",
                  "id": "9"
                },
                {
                  "type": "장비",
                  "id": "10"
                },
                {
                  "type": "container",
                  "id": "3",
                  "columns": [
                      {
                        "type": "장비",
                        "id": "13"
                      },
                      {
                        "type": "장비",
                        "id": "14"
                      }
                  ]
                },
                {
                  "type": "장비",
                  "id": "15"
                }
              ]
            }
          ]
        }
    };
  },
  computed: {},
  ready() {},
  attached() {},
  methods: {
    copied(index){
      index.id++;
    },
    inserted(data){
      // console.log(data);
    },
    toggleDisable() {
      this.disable = !this.disable;
    }
  },
  components: {
    list
  }
};
</script>

<style lang="less">
/**
 * For the correct positioning of the placeholder element, the dnd-list and
 * it's children must have position: relative
 */
.nestedDemo ul[dnd-list],
.nestedDemo ul[dnd-list] > li{
	position: relative;
  min-height: 40px;
}

/**
 * The dnd-list should always have a min-height,
 * otherwise you can't drop to it once it's empty
 */
.nestedDemo ul[dnd-list] {
    padding-left: 0px;
}

/**
 * The dndDraggingSource class will be applied to
 * the source element of a drag operation. It makes
 * sense to hide it to give the user the feeling
 * that he's actually moving it.
 */

.nestedDemo ul[dnd-list] .dndDragging{
    opacity: 0.7;
}

.nestedDemo ul[dnd-list] .dndDraggingSource{
    display: none;
}

/**
 * An element with .dndPlaceholder class will be
 * added to the dnd-list while the user is dragging
 * over it.
 */
.nestedDemo ul[dnd-list] .dndPlaceholder {
    display: block;
    background-color: #eee;
    min-height: 41px;
}

/**
 * The dnd-lists's child elements currently MUST have
 * position: relative. Otherwise we can not determine
 * whether the mouse pointer is in the upper or lower
 * half of the element we are dragging over. In other
 * browsers we can use event.offsetY for this.
 */
.nestedDemo ul[dnd-list] li {
    background-color: #fff;
    color: #35495E;
    border-bottom: 1px solid #41B883;
    display: block;
    padding: 0 15px;
    line-height: 40px;
    p{
      margin: 0;
    }
}

.nestedDemo ul[dnd-list] li:last-child{
    border-bottom: none;
    border-bottom-left-radius: 3px;
    border-bottom-right-radius: 3px;
}


/**
 * Show selected elements in green
 */
.nestedDemo ul[dnd-list] .selected {
    background-color: #dff0d8;
    color: #3c763d;
}
.nestedDemo ul[dnd-list] .selected.has-container {
  background-color: #fff;
  .panel-body, .panel-body li{
    background-color: #dff0d8;
    color: #3c763d;
  }
}


/*new-elements*/
.new-elements .dnd-button{
    border: 1px solid #41B883;
    text-align: center;
    height: 40px;
    border-radius: 4px;
    line-height: 40px;
    background-color: #41B883;
    color: #fff;
    cursor: move;
}

.disable-element .dnd-button{
    background-color: #E32822;
    border: 1px solid #E32822;
    cursor: pointer;
}

.selected{

}

.ashcan{
  .ashcan-logo{
    display: block;
    width: 40px;
    margin: 10px auto;
  }
}

/**
 * For the correct positioning of the placeholder element, the dnd-list and
 * it's children must have position: relative
 */
.horizontal ul[dnd-list],
.horizontal ul[dnd-list] > li{
	position: relative;
}

/**
 * The dnd-list should always have a min-height,
 * otherwise you can't drop to it once it's empty
 */
.horizontal ul[dnd-list] {
    padding-left: 0px;
		min-height: 40px;
}

/**
 * The dndDraggingSource class will be applied to
 * the source element of a drag operation. It makes
 * sense to hide it to give the user the feeling
 * that he's actually moving it.
 */

.horizontal ul[dnd-list] .dndDragging{
    opacity: 0.7;
}

.horizontal ul[dnd-list] .dndDraggingSource,
.horizontal .item-list .item.dndDraggingSource {
    display: none;
}

/**
 * An element with .dndPlaceholder class will be
 * added to the dnd-list while the user is dragging
 * over it.
 */
.horizontal ul[dnd-list] .dndPlaceholder {
    display: block;
    background-color: #eee;
    min-height: 41px;
}

.horizontal .container-list .dndPlaceholder{
  height: 100px;
}

.horizontal .item-list .dndPlaceholder{
  width: 50px;
  height: 50px;
  margin: 10px;
  padding: 5px;
  float: left;
  border-radius: 4px;
}

/**
 * The dnd-lists's child elements currently MUST have
 * position: relative. Otherwise we can not determine
 * whether the mouse pointer is in the upper or lower
 * half of the element we are dragging over. In other
 * browsers we can use event.offsetY for this.
 */
.horizontal .item-list li.item {
    color: #fff;
    display: block;
    width: 50px;
    height: 50px;
    margin: 10px;
    background: #55A1A8;
    text-align: center;
    line-height: 20px;
    padding: 5px;
    float: left;
    border-radius: 4px;
}

/**
 * Show selected elements in green  有问题的
 */
.horizontal ul[dnd-list] li.selected {
    background-color: #dff0d8;
    color: #3c763d;
}

</style>



